import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {path: '/', name: 'home', component: HomeView},
  {path: '/about', name: 'about', component: () => import('../views/AboutView')},
  {path: '/contact', name: 'contact', component: () => import('../views/ContactView')},
  {path: '/services', name: 'services', component: () => import('../views/ServiceView')},
  {path: '/services/web-development', name: 'webdevelopment', component: () => import('../views/WebDevelopment')},
  {path: '/services/web-design', name: 'webdesign', component: () => import('../views/WebDesign')},
  {path: '/services/mobile-app-development', name: 'mobileappdevelopment', component: () => import('../views/MobileappDevelopment')},
  {path: '/services/ims-solutions', name: 'imssolutions', component: () => import('../views/ImsSolutions')},
  {path: '/services/custom-php-development', name: 'customphpdevelopment', component: () => import('../views/CustomphpDevelopment')},
  {path: '/services/codeigniter-development', name: 'codeigniterdevelopment', component: () => import('../views/CodeigniterDevelopment')},
  {path: '/services/shopify-development', name: 'shopifydevelopment', component: () => import('../views/ShopifyDevelopment')},
  {path: '/services/wordpress-development', name: 'wordpressdevelopment', component: () => import('../views/WordpressDevelopment')},
  {path: '/services/android-app-development', name: 'androidappdevelopment', component: () => import('../views/AndroidappDevelopment')},
  {path: '/services/ios-app-development', name: 'iosappdevelopment', component: () => import('../views/IosappDevelopment')},
  {path: '/privacy-policy', name: 'privacypolicy', component: () => import('../views/PrivacyPolicy')},
  {path: '/terms-conditions', name: 'termsconditions', component: () => import('../views/TermsConditions')},
  {path: '/faq', name: 'faqs', component: () => import('../views/FaqView')},
  {path: '/careers', name: 'careers', component: () => import('../views/CareersView')},
  {path: '/case-studies', name: 'casestudies', component: () => import('../views/CaseStudies')},
  {path: '/plans', name: 'plans', component: () => import('../views/PlansView')},
  {path: '/blogs', name: 'blogs', component: () => import('../views/blog/BlogsView')},
]

// var generateSitemap = require('vue-router-tiny-sitemap')
// generateSitemap(routes,'https://www.itechvision.in')

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
